.location {
  margin-top: 2.2vw;
  width: 51vw;
  height: 74.5vh;
  @media screen and (min-width: 1024px) and (max-width: 1439px) {
    width: 60vw;
    height: 71.5vh;
  }
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 73vw;
    height: 71.5vh;
  }
  @media screen and (max-width: 767px) {
    width: 100vw;
    margin-top: 5vw;
    height: 100%;
  }
  .enterDataBlock {
    div {
      font-size: 1vw;
      line-height: 1.1vw;
      @media screen and (min-width: 1024px) and (max-width: 1439px) {
        font-size: 1.36vw;
      }
      @media screen and (min-width: 768px) and (max-width: 1023px) {
        font-size: 1.83vw;
        line-height: 1.9vw;
      }
      @media screen and (max-width: 767px) {
        font-size: 4.2vw;
      }
      span {
        color: #121212;
        font-weight: 300;
        margin-right: 1vw;
      }
      &:nth-child(1) {
        margin-left: 3.8vw;
        @media screen and (min-width: 1024px) and (max-width: 1439px) {
          margin-left: 5.18vw;
        }
        @media screen and (min-width: 768px) and (max-width: 1023px) {
          margin-left: 6.9vw;
        }
        @media screen and (max-width: 767px) {
          margin-left: 15.9vw;
        }
        select {
          background: none;
          border: none;
          border-bottom: 1px solid #999999;
          padding: 0.15vw 0.7vw;
          appearance: none;
          outline: none;
          font-style: normal;
          font-weight: 300;
          width: 15.56vw;
          @media screen and (min-width: 1024px) and (max-width: 1439px) {
            width: 18vw;
          }
          @media screen and (min-width: 768px) and (max-width: 1023px) {
            width: 25vw;
            line-height: 2.5vw;
          }
          @media screen and (max-width: 767px) {
            width: 60vw;
            line-height: 5vw;
          }
        }
      }
      &:nth-child(2) {
        margin-top: 1vw;
        input {
          background: none;
          border: none;
          border-bottom: 1px solid #999999;
          padding: 0.15vw 0.7vw;
          outline: none;
          width: 15.56vw;
          font-style: normal;
          font-weight: 300;
          @media screen and (min-width: 1024px) and (max-width: 1439px) {
            width: 18vw;
          }
          @media screen and (min-width: 768px) and (max-width: 1023px) {
            width: 25vw;
          }
          @media screen and (max-width: 767px) {
            width: 60vw;
            line-height: 5vw;
          }
        }
      }
    }
  }
  .mapBlock {
    margin-top: 2.5vw;
    span {
      color: #121212;
      font-weight: 300;
      font-size: 1vw;
      line-height: 1.1vw;
      @media screen and (min-width: 1024px) and (max-width: 1439px) {
        font-size: 1.36vw;
      }
      @media screen and (min-width: 768px) and (max-width: 1023px) {
        font-size: 1.83vw;
      }
      @media screen and (max-width: 767px) {
        font-size: 4vw;
      }
    }
    div {
      margin-top: 1vw;
      width: 51vw !important;
      height: 45vh !important;
      @media screen and (min-width: 1024px) and (max-width: 1439px) {
        width: 60vw !important;
        height: 40vh !important;
      }
      @media screen and (min-width: 768px) and (max-width: 1023px) {
        margin-top: 2vw;
        max-height: 45vh;
        width: 73vw !important;
        height: 50vh !important;
      }
      @media screen and (max-width: 767px) {
        margin-top: 2vw;
        max-height: 45vh;
        width: 88vw !important;
        height: 50vh !important;
      }
    }
  }
}
